<template>
  <el-dialog title="退款信息确认" :visible.sync="makeSureShow" :close-on-click-modal="false" :close-on-press-escape="false" width="50%">
    <el-form :model="form" class="demo-form-inline" label-width="120px">
      <div class="flex-row-start fw">
        <el-form-item label="退单编号：" style="width:50%;">
          <div class="mainFont">{{form.afterSaleNo}}</div>
        </el-form-item>
        <el-form-item label="退款原因：" style="width:50%;">
          <div class="mainFont">{{form.baseName ? form.baseName : '-'}}</div>
        </el-form-item>
        <el-form-item label="退货类型：" style="width:50%;">
          <div class="mainFont" v-if="form.type == 0">仅退款</div>
          <div class="mainFont" v-if="form.type == 10">退货退款</div>
          <div class="mainFont" v-if="form.type == 20">换货</div>
          <div class="mainFont" v-if="form.type == 30">维修</div>
        </el-form-item>
        <el-form-item label="退款方式：" style="width:50%;">
          <div class="mainFont">微信原路返回</div>
        </el-form-item>
        <el-form-item label="商品实付金额：" style="width:50%;">
          <div class="mainFont">￥{{form.payPrice}}</div>
        </el-form-item>
        <el-form-item label="运费：" style="width:50%;">
          <div class="mainFont">￥{{form.freightPrice}}</div>
        </el-form-item>
        <el-form-item label="是否扣除运费：" style="width:100%;" required>
          <el-radio v-model="subForm.isFreight" :disabled="form.flag == 1" label="1">退运费</el-radio>
          <el-radio v-model="subForm.isFreight" :disabled="form.flag == 1" label="0">不退运费</el-radio>
        </el-form-item>
        <el-form-item v-if="form.flag == 1" label="" style="width:100%;">
          <div style="width:100%;" class="dangerFont">
            该订单运费已经被扣除
          </div>
        </el-form-item>
        <div style="width:100%;">
          <el-form-item label="应退金额：" style="width:50%;">
            <div class="mainFont">￥{{subForm.isFreight == 0 ? form.payPrice : form.totalPayPrice}}</div>
          </el-form-item>
        </div>
        <div style="width:100%;">
          <el-form-item label="退款金额：" required>
            ￥<el-input-number 
              v-model="subForm.amount"
              :placeholder="subForm.isFreight == 0 ? ''+form.payPrice : ''+form.totalPayPrice" 
              :min="0" 
              :controls="false" :precision="2"></el-input-number>
          </el-form-item>
        </div>
        <div style="width:100%;">
          <el-form-item style="width:100%;" label="退款说明：">
            <el-input
              style="width:400px"
              type="textarea"
              placeholder="请输入退款说明"
              v-model="subForm.refundDesc"
              maxlength="100"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="退款备注：">
            <div style="color:#F56C6C;">
              如果部分退款，一定要与卖家提前沟通，经卖家同意，否则可能会造成投诉；
            </div>
          </el-form-item>
        </div>
      </div>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="confirmShowClose">取 消</el-button>
      <el-button :loading="makeSureLoading" type="primary" @click="confirmFun">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import {confirmDetail} from '@/api/afterSaleManage/returnRefund'
export default {
  props:['makeSureId','makeSureShow','makeSureLoading'],
  watch:{
    makeSureId(newvalue,oldValue){
      if(newvalue){
        console.log('执行了')
        this.getData()
      }
    }
  },
  data(){
    return{
      form:{},
      subForm:{
        isFreight:'0',
        refundDesc:''
      }
    }
  },
  methods:{
    getData(){
      confirmDetail({id:this.makeSureId}).then(res => {
        console.log('确认信息',res)
        if(res.code == 200){
          this.form = res.data
          if(res.data.flag == 1) this.subForm.isFreight = '0'  
          this.subForm.id = this.makeSureId
        }
      })
    },
    confirmFun(){
      console.log('this.subForm',this.subForm)
      if(this.subForm.amount == undefined || this.subForm.amount == null){
        this.$message.error('请输入退款金额');
        return;
      }
      // 判断运费
      if(this.subForm.isFreight == 0){
        if(this.subForm.amount > this.form.payPrice){
          this.$message.error('退款金额不可大于应退金额');
          return;
        }
      }
      if(this.subForm.isFreight == 1){
        if(this.subForm.amount > this.form.totalPayPrice){
          this.$message.error('退款金额不可大于应退金额');
          return;
        }
      }
      this.$emit('makeSureFun', this.subForm);
    },
    confirmShowClose(){
      this.$emit('makeSureClose',true)
    }
  }
}
</script>